.pagination{
    .page-item .page-link{
        border: 0;
        border-radius: 30px !important;
        transition: all .3s;
        padding: 0px 11px;
        margin: 0 3px;
        min-width: 30px;
        text-align: center;
        height: 30px;
        line-height: 30px;
        color: $white-color;
        cursor: pointer;
        font-size: $font-size-base;
        text-transform: uppercase;
        background: transparent;
        outline: none;

        &:hover,
        &:focus{
            background-color: rgba($white-color,0.1);
            border: none;
            box-shadow: none;
        }
    }

    .arrow-margin-left,
    .arrow-margin-right{
        position: absolute;
    }

    .arrow-margin-right{
        right: 0;
    }

    .arrow-margin-left{
        left: 0;
    }

    .page-item.active > .page-link{
        color: $white-color;
        box-shadow: $box-shadow;

        &,
        &:focus,
        &:hover{
          @include diagonal-gradient($primary-color, $primary-states-color);

            color: $white-color;
        }
    }

    .page-item.disabled > .page-link{
        opacity: .5;
    }

    // Colors
    &.pagination-info{
        .page-item.active > .page-link{
            &,
            &:focus,
            &:hover{
                @include diagonal-gradient($info-color, $info-states-color);
            }
        }
    }

    &.pagination-success{
        .page-item.active > .page-link{
            &,
            &:focus,
            &:hover{
                @include diagonal-gradient($success-color, $success-states-color);
            }
        }
    }

    &.pagination-primary{
        .page-item.active > .page-link{
            &,
            &:focus,
            &:hover{
              @include diagonal-gradient($primary-color, $primary-states-color);

            }
        }
    }

    &.pagination-warning{
        .page-item.active > .page-link{
            &,
            &:focus,
            &:hover{
              @include diagonal-gradient($warning-color, $warning-states-color);

            }
        }
    }

    &.pagination-danger{
        .page-item.active > .page-link{
            &,
            &:focus,
            &:hover{
              @include diagonal-gradient($danger-color, $danger-states-color);

            }
        }
    }

    &.pagination-neutral{
        .page-item > .page-link{
            color: $white-color;

            &:focus,
            &:hover{
                background-color: $opacity-2;
                color: $white-color;
            }
        }

        .page-item.active > .page-link{
            &,
            &:focus,
            &:hover{
                background-color: $white-bg;
                border-color: $white-bg;
                color: $brand-primary;
            }
        }
    }

}
